import { mapGetters, mapActions } from 'vuex';

export default {
  name: "routing",
  computed: {
      ...mapGetters({ snippets: 'codeSnippet/getRoutingSnippets' }),
  },
  methods: {
      ...mapActions({ loadSnippets: 'codeSnippet/loadRoutingSnippets' })
  },
  head() {
    return {
      title: "Vue Router",
      meta: [
        {
          hid: "description",
          name: "description",
          content: "glennpacker.net vue router and routing"
        }
      ]
    };
  },
  created () {
      this.loadSnippets();
  },
};
